import React, { useState } from 'react'
import { Form, Button } from 'react-bootstrap'

export const AddTodo = (props) => {
    const [title, setTitle] = useState("")
    const [desc, setDesc] = useState("")


    const submit = (e) => {
        e.preventDefault();
        if (!title || !desc) {
            alert("Title or description cannot be blank");
        }
        else {
            props.addTodo(title, desc);
            setTitle("")
            setDesc("")
        }
    }
    return (
        <div className="container my-3" style={{ border: "1px solid black" }}>
            <h3 align="left">Add a todo</h3>
            <Form onSubmit={submit}>
                <Form.Group className="mb-3" controlId="todoName">
                    <Form.Label>Todo Name</Form.Label>
                    <Form.Control as="textarea" rows={1} placeholder="Todo" value={title} onChange={(e) => { setTitle(e.target.value) }} />
                </Form.Group>
                <Form.Group className="mb-3" controlId="todoDesc">
                    <Form.Label>Todo description</Form.Label>
                    <Form.Control as="textarea" rows={3} placeholder="Description" value={desc} onChange={(e) => { setDesc(e.target.value) }} />
                </Form.Group>
                <Button className="mb-3" variant="primary" type="submit" size="sm">
                    Add
                </Button>
            </Form>
        </div>
    )
}
